@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&family=Rock+Salt&family=Spectral:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* For mobile */
.link a {
    text-decoration: none;
    margin-left: 10px;
    font-size: 50px;
    color: white;
    font-family: 'Source Sans Pro', sans-serif;
    font-weight: 200;
}

.iconColor {
    color: white;
}

.listItem {
    margin: 0;
    padding: 0px 20px;
    border: 0;
}

.navBar {
    background: #49494b;
    min-height: 80px !important;
}

.expandNav {
    color: white;
    margin-top: 20px;
    margin-bottom: 20px;
    padding: 20px 20px !important;
}

/* For desktop */
@media (min-width: 1000px) {
    .link a {
        font-size: 160%;
    }

    .navBar {
        min-height: unset;
    }
    
    .expandNav {
        margin-top: unset;
        margin-bottom: unset;
        padding: 10px 10px !important;
    }
}
.github a:hover {
    text-decoration: none;
    color: #49494b;
    border-bottom: 1px solid;
}

.footer {
    font-size: 20px;
}
.container {
    display: flex;
    max-width: 1300px;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}

.img {
    position: relative;
    padding-top: 30px;
    width: 100%;
    height: 100%;
}

.center {
    padding-top: 50px;
    justify-content: center;
}

.logo {
    display: block;
    text-align: center;
    margin: 0 auto;
}

.title {
    text-align: center;
    margin: 0 auto;
    font-size: 60px;
    letter-spacing: 10px;
    color: #49494b;
    font-family: 'Source Sans Pro', sans-serif;
    font-weight: 200;
    margin-top: 10px;
}

.imgText a {
    position: absolute;
    color: white;
    top: 8%;
    left: 10%;
    font-family: 'Source Sans Pro', sans-serif;
    font-weight: 400;
    letter-spacing: 5px;
    font-size: 35px;
}

.imgText a:hover {
    text-decoration: none;
    border-bottom: 1px solid;
}
.container {
    margin-top: 0px;
    display: flex;
    width: 100vw;
    justify-content: center;
    align-items: center;
}
